import React, { useState, useEffect } from 'react';
import './App.css';

import Encriptador from './Crypt'
import FormularioCorreo from './Mail';
import AudioPlayer from './Player';
function DetallesCriptomoneda({ criptomoneda }) {
  return ( 
    <div>
      <strong>{criptomoneda.symbol} - {criptomoneda.name}</strong><br />
      <blockquote>{criptomoneda.wallet}</blockquote>
      <p>
        <button onClick={() => window.open(criptomoneda.exchange, '_blank')}>Explorar {criptomoneda.symbol}</button>
      </p> 
     </div>
  );
}

function App() {
  const [datos, setDatos] = useState(null);
  const [criptomonedaSeleccionada, setCriptomonedaSeleccionada] = useState(null);

  useEffect(() => {
    // Funcin para obtener los datos de la API
    const obtenerDatosDeAPI = async () => {
      try {
        const response = await fetch('https://api.jsonbin.io/v3/b/64c339bbb89b1e2299c7006a');
        if (response.ok) {
          const data = await response.json();
          setDatos(data.record);
        } else {
          throw new Error('Error al obtener los datos de la API');
        }
      } catch (error) {
        console.error(error);
      }
    };

    obtenerDatosDeAPI();
  }, []);

  const handleSeleccionarCriptomoneda = (index) => {
    setCriptomonedaSeleccionada(index);
  };

  return (
    <div className="App">
      <header>
        <nav>
          <ul>
            <li><button onClick={() => setCriptomonedaSeleccionada(null)}>Inicio</button></li>
          </ul>
        </nav>
      </header>

      <main>
        <div className="container">

 
          {datos && datos.site && datos.site.title && (
            <div>
              <h1>{datos.site.title}</h1>
              <p>{datos.site.description}</p>
            </div>
          )}

          <AudioPlayer audioUrl="https://listen1.myradio24.com/5967" />

          <fieldset> 
          {datos && datos.cryptocurrencies && Array.isArray(datos.cryptocurrencies) ? (
            <>
              <legend>Donaciones  {criptomonedaSeleccionada !== null && (datos.cryptocurrencies[criptomonedaSeleccionada].symbol)}</legend>
              <div className="icon-container">
                {datos.cryptocurrencies.map((crypto, index) => (
                  <div key={index} className="icon-item" onClick={() => handleSeleccionarCriptomoneda(index)}>
                    <img src={crypto.icon} alt={crypto.name} style={{ width: "32px", height: "32px" }} />
                  </div>
                ))}
              </div>
              {criptomonedaSeleccionada !== null && (
                <DetallesCriptomoneda criptomoneda={datos.cryptocurrencies[criptomonedaSeleccionada]} />
              )}
            </>
          ) : (
            <p>Cargando datos...</p>
          )}
         </fieldset>
          <FormularioCorreo data={datos}/>


        </div>
      </main>
    </div>
  );
}

export default App;
