import React, { useRef, useEffect, useState, useMemo } from 'react';
 import Sketch from './Sketch'
// Helper function to format time in MM:SS format
const formatTime = (timeInSeconds) => {
  const minutes = Math.floor(timeInSeconds / 60);
  const seconds = Math.floor(timeInSeconds % 60);
  return `${minutes.toString().padStart(2, '0')}:${seconds.toString().padStart(2, '0')}`;
};

const AudioPlayer = ({ audioUrl }) => {
  const audioRef = useRef(null); 
  const [isPlaying, setIsPlaying] = useState(false);
  const [volume, setVolume] = useState(1.0);
  const [audioContext, setAudioContext] = useState(null);
  const [analyser, setAnalyser] = useState(null);
  const [elapsedTime, setElapsedTime] = useState(0); 

  

  useEffect(() => {
    const audioElement = audioRef.current;

    // Evento para pausar el audio
    const handlePause = () => {
      setIsPlaying(false);
      console.log('Audio pausado');
    };

    // Evento para reproducir el audio
    const handlePlay = () => {
      setIsPlaying(true);

      // Check if audioContext and analyser already exist
      if (!audioContext || !analyser) {
        const newAudioContext = new (window.AudioContext || window.webkitAudioContext)();
        const newAnalyser = newAudioContext.createAnalyser();
              newAnalyser.fftSize = 512;//1024 32
              newAnalyser.connect(newAudioContext.destination); 
              setAnalyser(newAnalyser);
        
        const source = newAudioContext.createMediaElementSource(audioElement);
              source.connect(newAnalyser);
              setAudioContext(newAudioContext);
        console.log('Audio Context and Analyser created.');
      }
      console.log('Audio reproducindose');
    };

  

    // Evento para cambiar el volumen del audio
    const handleVolumeChange = () => {
      const newVolume = audioElement.volume;
      setVolume(newVolume);
      console.log('Volumen cambiado:', newVolume);
    };

    // Evento para actualizar el tiempo transcurrido
    const handleTimeUpdate = () => {
      setElapsedTime(audioElement.currentTime);
    };

    audioElement.addEventListener('pause', handlePause);
    audioElement.addEventListener('play', handlePlay);
    audioElement.addEventListener('volumechange', handleVolumeChange);
    audioElement.addEventListener('timeupdate', handleTimeUpdate);

    return () => {
      // Limpia los eventos cuando el componente se desmonta
      audioElement.removeEventListener('pause', handlePause);
      audioElement.removeEventListener('play', handlePlay);
      audioElement.removeEventListener('volumechange', handleVolumeChange);
      audioElement.removeEventListener('timeupdate', handleTimeUpdate);
    };
  }, [audioContext, analyser]);

  const handlePlayButton = () => {
    if (isPlaying) {
      audioRef.current.pause();
    } else {
      audioRef.current.play();
    }
  };
 /*
  const analyserData = ()=>{
     if (analyser) {

      console.log('asdas');
       const bufferLength = analyser.frequencyBinCount;
       let dataArray = new Uint8Array(bufferLength);
       analyser.getByteFrequencyData(dataArray); 
      // dataArray = dataArray.slice(0, 5);
       return dataArray;
      }
   
  }*/
  const playButtonText = isPlaying ? `Reproduciendo (${formatTime(elapsedTime)})` : 'Play';
    
  return (
    <div> 
      {audioContext && analyser ? <Sketch audioContext={audioContext} analyser={analyser} /> : null}

    <fieldset>
      <legend>Reproducir Stream de Audio en React</legend>
      
      <div>
        <button onClick={handlePlayButton}>{playButtonText}</button>
        <input
          type="range"
          min="0"
          max="1"
          step="0.01"
          value={volume}
          onChange={(e) => {
            audioRef.current.volume = e.target.value;
            setVolume(e.target.value);
          }}
        />
      </div>

      <audio ref={audioRef} controls crossOrigin="anonymous" style={{ display: 'none' }}>
        <source src={audioUrl} type="audio/mpeg" />
        Tu navegador no soporta la reproduccin de audio en HTML5.
      </audio>
     
      <div>
      Estado del Reproductor:<br />
      Playing: {isPlaying.toString()}<br />
      Tiempo transcurrido: {formatTime(elapsedTime)}<br />
      Volumen: {volume} 
      </div>
    </fieldset></div>
  );
};

export default AudioPlayer;
