import React, { useState, useEffect } from 'react';
import axios from 'axios';

const FormularioCorreo = (datos) => {
  const [email, setEmail] = useState('');
  const [error, setError] = useState('');
  const [isSending, setIsSending] = useState(false);
  const [emails, setEmails] = useState([]);
   useEffect(() => {
    const storedEmails = localStorage.getItem('emails');
    if (storedEmails) {
      setEmails(JSON.parse(storedEmails));
    } else {
      fetchEmails();
    }
  }, []);

  const fetchEmails = async () => {
    try {
      const response = await axios.get('https://thunder-sulky-blanket.glitch.me/api/users');
      const emailsData = response.data;
      setEmails(emailsData);
      localStorage.setItem('emails', JSON.stringify(emailsData));
    } catch (error) {
      console.log('Error fetching emails:', error);
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;

    if (!emailRegex.test(email)) {
      setError('Ingresa un correo electrnico vlido.');
      return;
    }

    const allowedDomains = ['gmail.com', 'hotmail.com', 'prontomail.com', 'yahoo.com'];
    const domain = email.split('@')[1];
    if (allowedDomains.includes(domain)) {
      setError('Los correos de dominios como Gmail, Hotmail, Prontomail y Yahoo no estn permitidos.');
      return;
    }

    try {
      setIsSending(true);

      const response = await axios.post('https://thunder-sulky-blanket.glitch.me/api/users', {
        email: email,
      });

      setEmail('');
      setError('Correo enviado con xito!');

      const newEmail = response.data;
      const updatedEmails = [newEmail, ...emails];
      setEmails(updatedEmails);
      localStorage.setItem('emails', JSON.stringify(updatedEmails));
    } catch (error) {
      setError('Error al enviar el correo. Intntalo nuevamente.');
    } finally {
      setIsSending(false);
    }
  };

  const formatFecha = (fecha) => {
    const fechaObj = new Date(fecha);
    const dia = fechaObj.getDate();
    const mes = fechaObj.getMonth() + 1;
    const anio = fechaObj.getFullYear();
    return `${dia}/${mes}/${anio}`;
  };

  // Funcin para ordenar los correos electrnicos en orden descendente por el ID
  const ordenarCorreosPorIdDescendente = () => {
    return emails.slice().sort((a, b) => b.id - a.id);
  };

  return (
    <div>
      <form onSubmit={handleSubmit}>
        <fieldset>
          <legend>Correo electrnico:</legend>
           {datos.data && datos.data.blog && <p>{datos.data.blog[0].content}</p>}
           <input
            type="email"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
          />
       
        {error && <p style={{ color: 'red' }}>{error}</p>}
        <button type="submit" disabled={isSending}>
          {isSending ? 'Enviando...' : 'Enviar correo'}
        </button>
        </fieldset>
      </form>

      <h2>PENDIENTES</h2>
      {emails.length === 0 ? (
        <p>No hay correos almacenados.</p>
      ) : (
        <table>
          <thead>
            <tr>
              <th>ID</th>
              <th>Mail</th>
              <th>Fecha</th>
            </tr>
          </thead>
          <tbody>
            {ordenarCorreosPorIdDescendente().map((correo) => (
              <tr key={correo.id}>
                <td>{correo.id}</td>
                <td>{correo.email}</td>
                <td>{formatFecha(correo.date)}</td>
              </tr>
            ))}
          </tbody>
        </table>
      )}
    </div>
  );
};

export default FormularioCorreo;
