let bufferLength;
let dataArray;  
let centerX;
let centerY ;
const P5Wrapper = ({ audioContext ,analyser }) => {   
  /*
    useEffect(() => {
          bufferLength = analyser.frequencyBinCount;
          dataArray = new Uint8Array(bufferLength);
      // Acciones a realizar durante el montaje inicial (equivalente a componentDidMount)
      console.log('Componente: Montaje (equivalente a componentDidMount)');
      console.log(bufferLength);
      console.log(dataArray);

      // Acciones a realizar durante el desmontaje (equivalente a componentWillUnmount)
      return () => {
        console.log('Componente: Desmontaje (equivalente a componentWillUnmount)');
      };
    }, []); // El arreglo vaco asegura que este efecto solo se ejecute en el montaje y desmontaje
  
    useEffect(() => {
      // Acciones a realizar en actualizaciones posteriores (equivalente a componentDidUpdate)
      analyser.getByteFrequencyData(dataArray)
       console.log(dataArray);
        console.log('Componente: Actualizacin (equivalente a componentDidUpdate)');
        console.log('Componente: Render');
        // Agrega aqu cualquier otra lgica que necesites para actualizaciones
     
    });*/
     const setup = (p5, canvasParentRef) => { 
        bufferLength = analyser.frequencyBinCount;
        dataArray = new Uint8Array(bufferLength); 
 
        p5.createCanvas(p5.windowWidth, 500).parent(canvasParentRef);
        p5.background(220, 0);
        centerX = p5.width / 2;
        centerY = p5.height / 2;
      }; 

      
      const draw = (p5) => {
        if (typeof dataArray == 'object') {
            analyser.getByteFrequencyData(dataArray);
        }
        p5.clear();
        p5.background(220, 0); // Establece el fondo en un tono gris claro 
        p5.noFill();
        p5.stroke(0);
        p5.ellipse(centerX, centerY, 50 , 50);
    
        let numCircles = bufferLength; 
        for (let i = 0; i < numCircles; i++) {
            let targetCircleRadius = dataArray[i]; // Valor objetivo del radio en  
            // Distribuye los crculos en el eje x y los mueve en el eje y

            let espacio = p5.width / bufferLength;
            let x = espacio + i * (5 + espacio); 
            let y = -15; 
            // Genera un color verde brillante para cada crculo
            p5.fill(0, 255, 0);
            p5.noStroke();
            p5.rect(x, y, 5, targetCircleRadius)
            //p5.ellipse(x, y, 5, 5);
        }
    };
    
    
    
      return <Sketch setup={setup} draw={draw} />;
};

export default P5Wrapper;
